.foodko-top-header {
  position: fixed;
  z-index: 10;
  width: 100%;
  padding: 15px 0px 15px;
  transition: all .4s;
  background-color: var(--background-color2);
  @include max (991px){
   display: none;
}

  .foodko-top-header-content {
    ul {
      display: flex;
      justify-content: start;
      margin: 0;
      padding: 0;
      align-items: center;

      li {
        list-style: none;
        display: inline-flex;
        gap: 10px;
        gap: 10px;
        font-weight: 700;
        font-size: 18px;
        line-height: 28px;

        a {
          text-decoration: none;
          color: var(--black-color1);
          display: inline-flex;
          gap: 10px;
          padding-right: 24px;
          
          path {
            fill: var(--accent-color);
          }
        }
      }
    }
  }

  .foodko-top-header-content-2 {
  
  }
}











header.foodko-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 65px;
  padding: 15px 0 15px;
  transition: all .4s;
  background-color: var(--background-color);
  @include max (991px){
   top: 0;
 }
 &.foodko-header2{
  top: 0px;
 }
}


header.foodko-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--background-color);
}

header.foodko-header-section.foodko-header2{
  background-color: var(--background-color2);
}
header.foodko-header-section.foodko-header2.sticky-menu{
  background-color: var(--background-color2);
}


header.foodko-header-section.foodko-header3{
  background-color: var(--dark-bg);
}
header.foodko-header-section.foodko-header3.sticky-menu{
  background-color: var(--dark-bg);
}






