.foodko-blog-wrap{
    transition: all .4s;
    @include max (1199px){
        max-width: 600px;
        margin: 0 auto;
        padding-top: 24px;
          }
    a{
        .foodko-blog-thumb{
            overflow: hidden;
            height: 100%;
              &:hover .foodko-blog-wrap .foodko-blog-thumb::before {
            height: 100%;
            opacity: 0;
            transition: all 400ms linear;
          
          }
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        }
    }
    .foodko-blog-content {
        padding: 30px;
        border-radius: 0 0 10px 10px;
      border: 1px solid rgba(57, 61, 61, 0.2);
      border-top: none;
    
      @include max (767px){
       padding: 18px;
          }
        .foodko-blog-meta{
            ul{
                line-height: 1;
                display: flex;
              
                li{
                    position: relative;
                    padding-right: 30px;
                    gap: 5px;
                    display: flex;
                    align-items: center;
                  
                    a{
                        font-size: 15px;
                        color: var(--heading-color);
                    }
                }
            }
        }
        a{
          
        h4{
            margin: 24px 0px 10px;
            }
        }
        p{
            font-size: 18px;
            margin: 0;
        }
        a{
            &.foodko-blog-btn {
                font-weight: 500;
                display: flex;
                align-items: center;
                font-size: 18px;
                font-weight: 700;
                transition: all .4s;
                color: var(--heading-color);
                margin-top: 24px;
                gap: 8px;
              
                path {
                  fill: var(--heading-color);
                  transition: fill .4s; 
                }
              
                &:hover {
                  color: var(--accent-color);
                  font-weight: 700; 
                  path {
                    fill: var(--accent-color); 
                  }
                }
                @include max (767px){
                    margin-top: 15px;
                       }
              }
        }
    }
}