.foodko-hero-section {
    .foodko-center {
      align-items: center;
      position: relative;
      @include max(991px) {
        margin: 0 auto;
        max-width: 708px;
        text-align: center;
        justify-content: center;
      }
        .foodko-hero-img-box {
          position: relative;
          display: block;
          padding: 20px;
          z-index: 1;
          cursor: pointer;
          
          .foodko-hero-img {
            position: relative;
            display: block;
        
            &.inner {
        
            }
          }
        
          &:hover .foodko-hero-img-box .foodko-hero-img::before {
            height: 100%;
            opacity: 0;
            transition: all 400ms linear;
          }
        
          &:hover:after {
            background:  var(--background-color2);
          }
        
          &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0px;
            bottom: 0px;
            right: 0px;
            border-radius: 10px;
            background:var(--background-color);
            transition: all .2s linear;
            transition-delay: .1s;
            z-index: -1;
          }
        
          .foodko-hero-img .inner::before {
            background: var(--background-color);
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 0;
            opacity: 1;
            z-index: 9;
            pointer-events: none;
          }
        
        @include max(1199px) {
          max-width: 473px;
          margin: 0 auto;
          margin-top: 60px;
        }
  
        @include max(991px) {
          max-width: 349px;
          margin: 0 auto;
          margin-top: 50px;
        }
  
        h4 {
          max-width: 230px;
          padding-bottom: 20px;
        }
  
        
        .foodko-hero-img {
          a {
            display: block;
            position: relative;
            overflow: hidden;
            border-radius: 10px;
  
            img {
              width: 100%;
              height: auto;
              transition: transform 0.3s ease; 
            }
  
            &:before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              background-color: rgba(0, 0, 0, 0.1);
              z-index: 1;
              opacity: 0;
              transition: opacity 0.3s ease;
            }
          }
  
          &:hover {
  
            img {
              transform: scale(1.1);
            }
  
            a:before {
              opacity: 1; 
            }
          }
        }

      }
    }
    .foodko-hero-btn1 {
      display: inline-flex;
      gap: 24px;
      @include max (767px){
        display: inline-grid;
        gap: 10px;
    }
  }
  }














