.foodko-pricing-section{
    background-color: var( --background-color);
    .pricing-align{
        .foodko-pricing-box{
            display: flex;
            gap: 34px;
            margin-bottom: 24px;
            @include max (1399px){
                gap: 20px;
              }
              @include max (1199px){
                gap: 20px;
                align-items: center;
                max-width: 682px;
              }
              @include max (767px){
              display: contents;
              }
              &:hover {
                .foodko-pricing-box {
                  box-shadow: 0px 20px 4px rgba(0, 0, 0, 0.1); /* Add box shadow */
                }
      
                img {
                  transform: scale(1.1); /* Zoom out effect on hover */
                }
      
                a:before {
                  opacity: 1; /* Show overlay on hover */
                }
              }
            .foodko-pricing-img{
                flex-shrink: 0;
                a {
                    display: block;
                    position: relative;
                    overflow: hidden;
                    border-radius: 10px;
          
                    img {
                      width: 100%;
                      height: auto;
                      transition: transform 0.3s ease;
                    }
          
                    &:before {
                      content: '';
                      position: absolute;
                      top: 0;
                      left: 0;
                      right: 0;
                      bottom: 0;
                      background-color: rgba(0, 0, 0, 0.1);
                      z-index: 1;
                      opacity: 0;
                      transition: opacity 0.3s ease;
                    }
                  }
                
            }
            .foodko-pricing-content-box{
                @include max (767px){
                    padding: 30px 0px;
                    text-align: center;
                    }
                .foodko-pricing-content{
                    h4{
    
                    }
                    h5{
                        font-weight: 400;
                        margin: 10px 0px;
                    }
                }
                .foodko-pricing-btn{
                    display: inline-flex;
                    margin-top: 24px;
                }
            }
          
        }
    }
}


/*PRICING-SECTION-02 START*/


.foodko-pricing-section-2{
  .foodko-pricing-2-all{
    background-color: var(--background-color);
    padding: 30px;
    border-radius: 10px;
    position: relative;
    @include max(1199px) {
      margin-bottom: 40px
    }
    .MenuItem {
      display: block;
      position: relative;
      padding: 0;
      &:after {
        content: '';
        display: block;
        border-bottom: 1px dotted #000;
        position: relative;
        margin-top: -5px;
        z-index: 1;
      }
    
      &-table {
        position: relative;
        display: table;
        border-collapse: separate;
        background: transparent;
      }
    
      &-description {
        display: table-cell;
        position: relative;
        z-index: 2;
        float: left;
        width: 100%;
        margin-bottom: 0;
        vertical-align: bottom;
      }
    
      &-name {
        display: inline;
        margin: 0;
        background: var(--background-color);
        z-index: 2;
        position: relative;
        padding: 0 5px 0 0;
      }
    
      &-options {
        &:before {
          content: '';
          display: inline-block;
        }
      }
    
      &-price {
        width: 1%;
        white-space: nowrap;
        vertical-align: middle;
        display: table-cell;
        vertical-align: bottom;
      }
    
      &-priceValue {
        background-color: var(--background-color);
        display: inline-block;
        z-index: 2;
        padding: 0 0 0 15px;
        position: relative;
        font-size: 24px;
        color: var(--accent-color);
        font-weight: 700;
      }
    }
    p{
      margin: 0;
      padding: 15px 0px 35px;
      &:last-of-type{
        padding: 15px 0px 0px;
      }
    }
    
  }
  
}

.foodko-pricing-2-img-box{
  border-radius: 10px;
  background-repeat: no-repeat;
  padding: 86px 42px;
  position: relative;
  margin-bottom: 23px;
  background-size: cover;
  &:last-of-type{
    margin-bottom: 0;
  }
&.foodko-pricing-2-img-box:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(10, 15, 15, .6);
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;

}
  .foodko-pricing-2-content{
    text-align: center;
    position: relative;
    h3{
      color: var(--white-color);
    }
    h5{
      color: var(--white-color2);
      font-weight: 400;
      padding: 10px 0px 15px;
      max-width: 200px;
      margin: 0 auto;
    }
    h2{
      color: var(--background-color2);
      font-size: 50px;
    }
    .foodko-pricing-2-btn{
      padding-top: 35px;
       display: inline-flex;
       button#foodko-pricing-btn {
        border-radius: 6px;
        display: flex;
        padding: 15px 69px;
        @include max(1399px) {
          padding: 15px 42px;
        }
        @include max(767px) {
          padding: 15px 32px;
        }
    }
    }
  }
}



/*PRICING-SECTION-02 end*/








