
/*--about-section start*/
.foodko-about-center {
  position: relative;
  align-items: center;
  @include max(991px) {
    text-align: center;
    margin: 0 auto;
  }

  .foodko-site-title-text2 {
    display: flex;

    @include max(1199px) {
      display: contents;
    }
  }
}
.foodko-about-btn {
  display: inline-flex;
  padding: 50px 0px 0px;

  @include max(1199px) {
    padding: 30px 0px 0px;
  }
}
/*--about-section end*/

/*--page 2--about-section-02 start*/
.foodko-about-icon-all{
  display: flex;
  @include max(767px) {
    display: contents;
  }
  .foodko-site-title-box{
    padding-right: 50px;
    @include max(767px) {
      padding-right: 0px;
      padding-bottom: 20px;
    }
  }
}

/*--page 2--about-section-02 end*/