/* top scroll */
.paginacontainer{
  .progress-wrap{
    position: fixed;
    right: 30px;
    bottom: 30px;
    height: 50px;
    width: 50px;
    cursor: pointer;
    display: block;
    border-radius: 50px;
    box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.04);
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    transform: translateY(15px);
    -webkit-transition: all 1ms linear;
      transition: all 200ms linear;
    &.active-progress{
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      .progress-circle path{
        stroke: #0D0E1D; 
        stroke-width: 4;
        box-sizing:border-box;
        -webkit-transition: all 200ms linear;
          transition: all 200ms linear;
      }
    }
    svg path{
      fill: none; 
      stroke: #000;
    }
    .top-arrow{
      top: 50%;
      left: 50%;
      position: absolute;
      transform: translate(-50%, -50%);
    }
  }
}

/* top scroll */


/* image-section-start */

.foodko-img-section {
  padding-right: 26px;
  position: relative;

  @include max(1199px) {
    padding-right: 0;
    padding-bottom: 24px;
    text-align: center;
  }
  &.one{
    padding-left: 30px;
    padding-right: 0;
    @include max (1199px){
      padding-left: 0px;
     }
  }
  a {
    display: block;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer; /* Add cursor pointer */

    img {
      width: 100%;
      height: auto;
      transition: transform 0.3s ease;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.1);
      z-index: 1;
      opacity: 0;
      transition: opacity 0.3s ease;
    }

    &:hover {
      img {
        transform: scale(1.1); /* Zoom out effect on hover */
      }

      &:before {
        opacity: 1; /* Show overlay on hover */
      }
    }
  }
}
/* image-section-start */