/*form-SECTION START*/

.foodko-form-section{
  background: var(--white-color);
  padding: 50px;
  border-radius: 10px;
  gap: 24px;
  display: grid;
  margin-left: 26px;
  &.two{
    background: none;
    padding: 0;
   .foodko-form-title{
   h4{
    color: var(--white-color);
    font-size: 40px;
    font-weight: 700;
   }
   }
   .contact-page__input-box{
    border: 1px solid var(--white-color2);
    
  
    input{
      background-color: var(--heading-color);
      color: var(--white-color);
      &::placeholder {
        color: var(--heading-colo); 
    }
    }
   }

  }
  @include max (1199px){
    margin-left: 10px;
    }
    @include max (991px){
      margin-left: 0px;
      margin-top: 24px;
      }
      .contact-page__input-box{
        position: relative;
        width: 100%;
        border: 1px solid var( --body-color);
        border-radius: 3px;
        img{
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          height: auto;
          width: 24px;
          left: 17px;
        }
        input{
          width: 100%;
          padding: 15px 40px 15px 49px;
          border: none;
          outline: none;
          &::placeholder {
            color: var(--heading-colo); 
        }
        }
      }
  button#foodko-form-btn {
    width: 100%;
    padding: 10px;
    font-size: 18px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .4s;
    border-radius: 3px;
}

}
/*form-SECTION end*/



/*BOOKING-SECTION START*/
.foodko-booking-section{
  background-color: var(--background-color);
  .foodko-booking-align{
    @include max (991px){
      text-align: center;
      }
    .foodko-booking-img{
    }
  
  }
 
}
/*BOOKING-SECTION END*/

/*CONTACT-SECTION START*/

.foodko-contact-section{
  background-color: var( --heading-color);
 
  .foodko-contact-content{
    @include max (1199px){
     max-width: 610px;
       }
      .foodko-contact-text{
          h2{
              color: var(--white-color);
              font-family: $heading-font;
          }
          p{
              color: var(--white-color2);
              margin: 18px 0px 50px;
              @include max (1199px){
               margin: 15px 0px 30px;
                }
          }
      }
      .foodko-contact-call{
          display: flex;
          align-items: center;
          gap: 15px;
          @include max (1199px){
          margin-bottom: 40px;
          }
          .foodko-contact-icon{

          }
          .foodko-cotact-text{
              p{
                  font-size: 20px;
                  margin-bottom: 8px;
                  color: var(--white-color2);
              }
              h3{
                  color: var(--white-color);
              }
          }
      }
  }
}

/*CONTACT-SECTION END*/



















.foodko-subscription2{
  @include max(1199px) {
   padding-top: 24px;
    }
  &.one{
      max-width: 508px;
  }
  h3{
      padding-bottom: 30px;
      }
      form{
          position: relative;
          width: 516px;
          @include max(767px) {
              width: 334px;
            }
          &.max{
              width: 100%;
          }
          input{
              height: 60px;
              font-size: 16px;
              font-weight: 700;
             padding: 15px 190px 15px 17px;
              border: 1px solid #eaedf0 !important;
              width: 100%;
              border-radius: 3px;
             &:focus{
                border: 1px solid #eaedf0!important;
              }
              @include max(767px) {
                width: 100%;
                margin-bottom: 20px;
              }
              
       &::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
          color:    var(--#eaedf0);
          opacity: 1;
       }
       &:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
         color:    var(--body-color);
         opacity:  1;
       }
       &::-moz-placeholder{ /* Mozilla Firefox 19+ */
         color:    var(--body-color);
         opacity:  1;
       }
       &:-ms-input-placeholder{ /* Internet Explorer 10-11 */
         color:    var(--body-color);
         opacity: 1;
       }
       &::-ms-input-placeholder{ /* Microsoft Edge */
         color:    var(--body-color);
         opacity: 1;
       }
       
       &::placeholder{ /* Most modern browsers support this now. */
         color:    var(--body-color);
         opacity: 1;
       }
       
          }
          .foodko-footer-btn{
              position: absolute;
              right: 0;
              top: 2px;
          
          }
          
      }
  

}