h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $heading-font;
  font-family: $body-font;
  color: var(--heading-color);
  font-weight: 700;
  margin: 0;
}
h1,
.h1 {
  font-size: 110px;
  letter-spacing: -0.5px;
  line-height: 120px;
  @include max(1400px) { 
    font-size: 110px;
  }
  @include max(1399px) { 
    font-size: 93px;
  }
  @include max(1199px) { 
    font-size: 90px;
  }
  @include max(767px) { 
    font-size: 44px;
   line-height: 55px;
  }
}
h2,
.h2 {
  font-size: 60px;
  line-height: 66px; 
  letter-spacing: -0.5px;
  @include max(1199px) { 
    font-size: 48px;
    line-height: 55px;
  }
  @include max(767px) { 
    font-size: 36px;
    line-height: 46px;
    font-weight: 700;
  }
}

h3,
.h3 {
  font-size: 30px;
  line-height:35px;
  letter-spacing: -0.5px;
  @include max(767px) {
    font-size: 24px;
  }

}
h4,
.h4 {
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -0.5px;
}
h5,
.h5 {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: -0.5px;
  @include max(767px) { 
    font-size: 18px;
    line-height: 25px; 
    letter-spacing: -0.5%;
  }
}
h6,
.h6 {
  font-size: 14px;
}

p{
  font-size: 21px;
  line-height: 30px;
  letter-spacing: -0.5px;
  @include max(767px) {
  }
  &:last-child{

  }
}


@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}



.gray-bg{
  background-color: var(--background-color);
}
.yellow-bg{
  background-color: var(--background-color2);
}