
/*--icon box--*/
.foodko-icon-box{
    &.one{
        border: 1px solid var(--light-bg);
        padding: 30px;
        text-align: center;
        border-radius: 5px;
        position: relative;
        z-index: 0;
        @include max (1199px){
            max-width: 500px;
            margin: 0 auto;
            margin-top: 24px;
          }

          &:before {
            content: '';
            position: absolute;
           bottom: 0;
            left: 0;
            width: 100%;
            height: 0;
            background-color: var( --accent-color);
            z-index: -1;
            opacity: 0;
            transition:  0.4s;
            border-radius: 5px;
          }
          &:hover::before{
            height: 100%;
            opacity: 1;
          }
          
        &:hover{
            color: var(--white-color2);
            transition: 0.3s;
            .foodko-icon-img path {
                fill: var(--white-color);
                transition: 0.3s;
            }
            h4{
                color: var(--white-color);
                transition: 0.3s;
            }
        }

    }
        &.two{
            display: flex;
            gap: 15px;
            @include max (1199px){
                display: grid;
                padding-top: 20px;
              }
        }
        &.three{
            background-color: var(--background-color);
            border-radius: 5px;
            padding: 30px;
            display: flex;
            align-items: center;
            gap: 29px;
            @include max(1399px) {
                padding: 22px;
                gap: 20px;
              }
              @include max(1199px) {
                max-width: 466px;
                margin: 0 auto;
                margin-bottom: 20px;
               
              }
              @include max(991px) {
                padding: 14px;
                gap: 14px;
              }
        }
        &.four{
            display: flex;
            align-items: center;
            gap: 15px;
            margin-bottom: 30px;
            &:last-of-type{
                margin-bottom: 0;
            }
        }
        &.five{
            display: flex;
            align-items: center;
            gap: 10px;
            margin-bottom: 15px;
            &:last-of-type{
                margin-bottom: 0;
            }
        }
        &.six{
            border: 1px solid rgba(57, 61, 61, 0.2);
            border-radius: 10px;
            padding: 30px;
            @include max(1199px) {
                max-width: 600px;
                margin: 0 auto;
                margin-top: 24px;
              }
            .foodko-icon-img {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(57, 61, 61, 0.2);
                h4{
                    font-family: $heading-font;
                }
            }
            p{
              padding-top: 20px;
            }
        }
        .foodko-icon-img{
            flex-shrink: 0;
            path{
              fill: var(--heading-color);
            }
        }
        .foodko-icon-text{
            &.two{
                h4{
                    padding: 0px 0px 10px;
                    font-family: $heading-font;
                }
                p{
                    font-size: 18px;
                    line-height: 28px;
                }
            }
            &.three{
                h4{
                    padding: 0px 0px 10px 0px;
                }
            }
           &.four{
            h4{
                padding: 0;
            }
           }
            h4{
                padding: 25px 0px 15px;
               
            }
            p{
                margin: 0;
            }
        }
    
}
/*--icon box-end-*/