
.foodko-portfolio-box{
    display: block;
 
        @include max (1199px){
            display: ruby-text;
            padding-top: 24px;
              }
    .foodko-portfolio-content {
        position: relative;
        width: 100%;
        max-width: 400px;
        margin: auto;
        overflow: hidden;
        margin-bottom: 24px;
        @include max (1199px){
         margin-bottom: 0;
              }
        &:last-of-type{
            margin-bottom: 0px;
        }
      
        .foodko-portfolio-content-overlay {
          background: rgba(0, 0, 0, 0.6);
          position: absolute;
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          opacity: 0;
          transition: all 0.4s ease-in-out 0s;
          border-radius: 10px;
        }
      
        &:hover .foodko-portfolio-content-overlay {
          opacity: 1;
        }
      
        .content-image {
          width: 100%;
        }
      
        .foodko-portfolio-content-details {
          position: absolute;
          text-align: center;
          padding-left: 1em;
          padding-right: 1em;
          width: 100%;
          top: 50%;
          left: 50%;
          opacity: 0;
          transform: translate(-50%, -50%);
          transition: all 0.3s ease-in-out 0s;
        }
      
        &:hover .foodko-portfolio-content-details {
          top: 50%;
          left: 50%;
          opacity: 1;
        }
      
        .foodko-portfolio-content-details h3 {
          color: var(--white-color);
          font-weight: 700;
          margin-bottom: 8px;
        }
      
        .foodko-portfolio-content-details p {
          font-size: 18px;
          font-weight: 700;
        }
      
        .text-star {
          display: flex;
          align-items: center;
          text-align: center;
          justify-content: center;
          gap: 5px;
      
          p {
            margin: 0;
            color: var(--white-color);
          }
      
          img {
            flex-shrink: 0;
          }
        }
      
        .fadeIn-bottom {
          top: 80%;
        }
      }
}




