.foodko-team-section{
    background-color: var(--background-color);
    position: relative;
    .foodko-team-box{
        @include max(1199px) {
            max-width: 416px;
            margin: 0 auto;
            margin-top: 24px;
          }
   
        .foodko-team-content{
          background-color: var(--white-color);
            align-items: center;
            text-align: center;
            padding: 30px;
            border-radius: 10px;
            .foodko-team-img{

            }
            .foodko-team-icon{
                text-align: center;
                padding-top: 24px;
                .foodko-social-icon{

                }
                .foodko-team-text{
                    h4{
                        padding: 15px 0px 6px;
                        font-family: $heading-font;
                    }
                    p{
                        margin: 0;
                    }
                }
            }
        }
    }
}