 
// footer one
 
 .foodko-footer-center{
    padding-bottom: 70px;
.foodko-footer-wrap{
    p{
        margin: 0;
        padding: 30px 0px 40px;
    }
    .foodko-social-icon{
      
    }
}
.foodko-footer-menu{
    @include max(1199px) {
        padding-top: 24px;
        &:nth-of-type(2) {
            padding-top: 0px; 
        }
      }
    h4{

    }
    ul{
        padding-top: 30px;
        @include max(991px) {
            padding-top: 10px;
          }
        li{
            padding-top: 13px;
        
            a{
                font-size: 21px;
                color: var(--body-color);
            }
            &.foodko-time{
                display: flex;
                justify-content: space-between;
                &.one{
                    border-bottom: 1px solid var(--light-bg);
                    &:nth-child(3){
                        border-bottom: none;
                    }
                }

                span{
                    
                }
                p{
                    font-weight: 700;
                    &.font-weight{
                        font-weight: 400;
                    }
                }
            }
        }
    }
}
}
.foodko-footer-bottom.center {
    text-align: center;
    padding: 35px 0px 0px;
    border-top: 1px solid var(--light-bg);
}






