.foodko-counter-section {
    background-color: var(--background-color);
    position: relative;
}



.foodko-counter-all {
    display: flex;
    justify-content: space-between;
    text-align: center;
    @include max (1199px) {
        text-align: center;
    }
    @include max(991px) {
        display: ruby;
      }
    .foodko-counter-single-box {
        .foodko-counter-data2 {
            
            h1 {
                font-family: $heading-font;
                span {
                    // Add your styles for the counter span here
                }
            }

            p {
                padding-top: 15px;
            }
            &.data3{
                h2{
                    font-family: $heading-font;
                    color: var(--accent-color);
                    span {
                        color: var(--accent-color);
                    }
                }
                p{
                    margin-bottom: 0;
                    padding-top: 10px;
                }
            }
        }

    }
}
