// fonts variable
$body-font:  "Inter", sans-serif !default;
$heading-font: "Playfair Display", serif !default;


// color variable
:root {
  --heading-color: #0A0F0F;
  --body-color: #393D3D;
  --white-color: #FFFFFF;
  --background-color:#FAF7F2;
  --white-color2:#FDFDFD;
  --background-color2: #FFD40D;
  --accent-color:#EB0029;
  --dark-bg:#0A0F0F;
  --light-bg: #eaedf0;
  --black-color: #1A1C20;
  --black-color1:#000;

  // --light-color2: #DBDBDB;
  // --green-bg: #0C9347;
  // --grey-color:  #747474;
}

