.foodko-auto-text-slider-section{
    background-color: var( --accent-color);
    padding: 31px 0px;
    align-items: center;
    .swiper-wrapper{
        margin: 0;
        padding: 0;
        .swiper-slide{
            .foodko-auto-slider-item{
                h2{
                    display: flex;
                    gap: 40px;
                    font-size: 80px;
                    letter-spacing: 0.5%;
                    font-family: $heading-font;
                    align-items: center;
                    color: var(--white-color);
                    @include max (1399px){
                      font-size: 58px;
                    }
                    @include max (1199px){
                        font-size: 55px;
                      }
                      @include max (767px){
                        font-size: 27px;
                        gap: 20px;
                      }
                }
            }
        }
    }
}